import React, {useEffect} from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import { Checkbox } from '@material-ui/core';
import { FormControlLabel, FormControl, InputLabel, Select, MenuItem } from '@material-ui/core';
import { useDispatch, useSelector } from "react-redux";
import { deleteUser, filterUsersByRole, filterUsersByStatus, loadUsers, sortDateAsc, sortDateDesc, sortUsersAsc, sortUsersDesc } from '../redux/actions';
import { useNavigate } from "react-router-dom";

const useButtonStyle = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    marginTop: 0,
    minWidth: 900,
  },
});

const Home = () => {
  const classes = useStyles();
  const buttonStyles = useButtonStyle();
  let dispatch = useDispatch();
  const navigate = useNavigate();
  const {users} = useSelector(state => state.data);

  useEffect(() => {
    dispatch(loadUsers());
  }, []);

  const handleDelete = (id) => {
    if(window.confirm(" ,    ?")) {
      dispatch(deleteUser(id));
    }
  };

  const handleSortDesc = () => {
    dispatch(sortUsersDesc());
  };
  
  const handleSortAsc = () => {
    dispatch(sortUsersAsc());
  };

  const handleSortDateDesc = () => {
    dispatch(sortDateDesc());
  };
  
  const handleSortDateAsc = () => {
    dispatch(sortDateAsc());
  };

  const handleFilterByRole = (e) => {
    dispatch(filterUsersByRole(e.target.value));
  };

  const handleFilterByStatus = (e) => {
    dispatch(filterUsersByStatus(e.target.checked));
  };

  return (
    <div>
      <div className={buttonStyles.root}>
        <Button 
        variant="contained" 
        color="primary" 
        onClick={() => navigate("/addUser")}
        >
           
        </Button>
      </div>

      <div>
      <ButtonGroup>
        <Button onClick={handleSortDesc}>   ( )</Button>
        <Button onClick={handleSortAsc}>   ( )</Button>
        <Button onClick={handleSortDateDesc}>   ( )</Button>
        <Button onClick={handleSortDateAsc}>   ( )</Button>
      </ButtonGroup>

      <br/>

      <FormControl className={classes.formControl} style={{width: "200px"}}>
                <InputLabel id="demo-simple-select-label">  </InputLabel>
                <Select
                align="left"
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                onChange={handleFilterByRole}
                >
                    <MenuItem value={'waiter'}>waiter</MenuItem>
                    <MenuItem value={'cook'}>cook</MenuItem>
                    <MenuItem value={'driver'}>driver</MenuItem>
                </Select>
      </FormControl>

      <FormControlLabel
                  control={<Checkbox onChange={handleFilterByStatus}/>}
                  label="  "
                />
      </div>

      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell align="center"></StyledTableCell>
              <StyledTableCell align="center"></StyledTableCell>
              <StyledTableCell align="center"> </StyledTableCell>
              <StyledTableCell align="center"> </StyledTableCell>
              <StyledTableCell align="center"></StyledTableCell>
              <StyledTableCell align="center"></StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users && users.map((user) => (
              <StyledTableRow key={user.id}>
                <StyledTableCell component="th" scope="row">
                  {user.name}
                </StyledTableCell>
                <StyledTableCell align="center">{user.role}</StyledTableCell>
                <StyledTableCell align="center">{user.phone}</StyledTableCell>
                <StyledTableCell align="center">{user.birthday}</StyledTableCell>
                <StyledTableCell align="center">
                <FormControlLabel
                  control={<Checkbox checked={user.isArchive} disabled/>}
                  label=" "
                />
                </StyledTableCell>
                <StyledTableCell align="center">
                  <div className={buttonStyles.root}>
                    <ButtonGroup variant="contained" aria-label="contained primary button group">
                      <Button color="secondary" onClick={() => handleDelete(user.id)}></Button>
                      <Button color="primary" onClick={() => navigate(`/editUser/${user.id}`)}></Button>
                    </ButtonGroup>
                  </div>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      

    </div>
  )
}

export default Home;